          target="_blank"
        >
          awesome-vue
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App and Express',
      msg2: ''
    }
  },
  mounted () {
    this.getinfo()
  },
  methods: {
    getinfo () {
      // this.$http.get('/api/getinfo').then((response) => {
      this.$http.get('/api/getInit').then((response) => {
        this.msg2 = response.data
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



